.creator {
  font-weight: 400;
}

.creator-inner {
  display: flex;
  padding: 20px 0;
  border-bottom: 1px solid #cccccc;
}

.creator-part {
  width: 55%;
  margin-right: 30px;
  border-right: 1px solid #cccccc;
}

.form-wrapper {
  width: 45%;
}

.questions {
  padding: 10px 30px 10px 10px;
}

.questions-count {
  color: var(--accent-color);
  font-weight: bold;
  display: inline-block;
  margin-left: 30px;
  border: 1px solid #ccc;
  font-size: 1.2em;
  padding: 7px 12px;
}

.point  {
  margin-right: 30px;
}

.info-text {
  font-size: 1.3em;
  font-weight: 500;
}

.error-container {
  text-align: center;
  padding: 10px;
  color: red;
}

@media (max-width: 767px) {
  .creator-inner {
    flex-wrap: wrap;
  }
  .creator-inner > div {
    width: 100%;
    margin: 10px 0;
  }
}